const palette_array = [
  0x89, 0x50, 0x4e, 0x47, 0x0d, 0x0a, 0x1a, 0x0a, 
  0x00, 0x00, 0x00, 0x0d, 0x49, 0x48, 0x44, 0x52, 
  0x00, 0x00, 0x00, 0x08, 0x00, 0x00, 0x00, 0x01, 
  0x08, 0x03, 0x00, 0x00, 0x00, 0xd4, 0xde, 0x1f, 
  0x71, 0x00, 0x00, 0x00, 0x01, 0x73, 0x52, 0x47, 
  0x42, 0x00, 0xae, 0xce, 0x1c, 0xe9, 0x00, 0x00, 
  0x00, 0x04, 0x67, 0x41, 0x4d, 0x41, 0x00, 0x00, 
  0xb1, 0x8f, 0x0b, 0xfc, 0x61, 0x05, 0x00, 0x00, 
  0x00, 0x18, 0x50, 0x4c, 0x54, 0x45, 0xff, 0xff,
  0xff, 0xff, 0xff, 0xff, 0xff, 0xff, 0xff, 0xff, 
  0xff, 0xff, 0xff, 0xff, 0xff, 0xff, 0xff, 0xff, 
  0xff, 0xff, 0xff, 0xff, 0xff, 0xff, 0x01, 0x7a, 
  0x33, 0x61, 0x00, 0x00, 0x00, 0x09, 0x70, 0x48, 
  0x59, 0x73, 0x00, 0x00, 0x0e, 0xc3, 0x00, 0x00, 
  0x0e, 0xc3, 0x01, 0xc7, 0x6f, 0xa8, 0x64, 0x00, 
  0x00, 0x00, 0x11, 0x49, 0x44, 0x41, 0x54, 0x18, 
  0x57, 0x63, 0x60, 0x60, 0x64, 0x62, 0x66, 0x61, 
  0x65, 0x63, 0x07, 0x00, 0x00, 0x5d, 0x00, 0x1d, 
  0xe8, 0x07, 0xc2, 0xb7, 0x00, 0x00, 0x00, 0x00, 
  0x49, 0x45, 0x4e, 0x44, 0xae, 0x42, 0x60, 0x82]

  const generateGradient = (startColor, endColor) => {
    const start = {
      r: parseInt(startColor.slice(1, 3), 16),
      g: parseInt(startColor.slice(3, 5), 16),
      b: parseInt(startColor.slice(5, 7), 16)
    };
    const end = {
      r: parseInt(endColor.slice(1, 3), 16),
      g: parseInt(endColor.slice(3, 5), 16),
      b: parseInt(endColor.slice(5, 7), 16)
    };
    const diff = {
      r: end.r - start.r,
      g: end.g - start.g,
      b: end.b - start.b
    };
    const step = {
      r: diff.r / 7.0,
      g: diff.g / 7.0,
      b: diff.b / 7.0
    };
    const gradient = [];
    for (let i = 0; i < 8; i++) {
      const color = {
        r: start.r + step.r * i,
        g: start.g + step.g * i,
        b: start.b + step.b * i
      };
      gradient.push(
        "#" +
          Math.round(color.r).toString(16).padStart(2, "0") +
          Math.round(color.g).toString(16).padStart(2, "0") +
          Math.round(color.b).toString(16).padStart(2, "0")
      );
    }
    return gradient;
  }
  
  const snakeToTitle = (str) => {
    return str.replace(/_/g, ' ')
      .replace(/\w\S*/g, function(txt) {
        return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
      });
  }

function App() {
  const [dataList, setDataList] = useState([]);
  const [pickers, setPickers] = useState([]);
  const [pickerCount, setPickerCount] = useState(1);
  const [packName, setPackName] = useState('CustomTrims');

  const saveResources = (e) => {
    e.preventDefault();
    let tmpDataList = dataList;

    const zip = new JSZip();
    const resourceRoot = zip.folder(`${packName}ResourcePack`);
    const assets = resourceRoot.folder("assets");
    const minecraft = assets.folder("minecraft");
    const atlases = minecraft.folder("atlases");
    const models = minecraft.folder("models");
    const item = models.folder("item");
    const textures = minecraft.folder("textures");
    const trims = textures.folder("trims");
    const color_palettes = trims.folder("color_palettes");

    const dataRoot = zip.folder(`${packName}DataPack`);
    const data = dataRoot.folder("data");
    const data_minecraft = data.folder("minecraft");
    const tags = data_minecraft.folder("tags");
    const items = tags.folder("items");
    const trim_material = data_minecraft.folder("trim_material");

    // Resource Pack
    // MCMETA file in root directory;
    let resourceMcmeta = resourceMetadata;
    resourceMcmeta = resourceMcmeta.replaceAll('PACKNAME', packName);
    resourceRoot.file("pack.mcmeta",resourceMcmeta);

    // Files in atlases
    // Armor Trim Atlas
    let armorTrims = atlasArmorTrimTemplate;
    let atlasLines = '';

    for (let dataEntry of tmpDataList)
    {
      let thisLine = atlasArmorTrimLineTemplate;
      thisLine = thisLine.replaceAll("TRIMNAME",dataEntry.trimName);
      atlasLines = atlasLines + thisLine;
    }
    
    armorTrims = armorTrims.replaceAll('ATLASLINES',atlasLines);
    atlases.file("armor_trims.json",armorTrims);

    // Block Trim Atlas
    let blocks = atlasBlocksTemplate;
    let blockLines = '';

    for (let dataEntry of tmpDataList)
    {
      let thisLine = atlasBlocksLineTemplate;
      thisLine = thisLine.replaceAll("TRIMNAME",dataEntry.trimName);
      blockLines = blockLines + thisLine;
    }
    
    blocks = blocks.replaceAll('BLOCKLINES',blockLines);
    atlases.file("blocks.json",blocks);

    // Files in textures/trims/color_palettes
    for (let dataEntry of tmpDataList)
    {
      let png_buffer = [...palette_array];
      let index = 70;

      for (let color of dataEntry.gradientColors)
      {
          png_buffer[index++] = parseInt(color.slice(1, 3), 16);
          png_buffer[index++] = parseInt(color.slice(3, 5), 16);
          png_buffer[index++] = parseInt(color.slice(5, 7), 16);
      }

      color_palettes.file(`${dataEntry.trimName}.png`,png_buffer);
    }

    // Files in models.item
    for (const material of materials)
    {
      for (const armorType of armorTypes)
      {
        if (material !== 'turtle' || armorType === 'helmet')
        {
          let armorModel = armorModelTemplate;
  
          let modelEntries = '';
  
          for (let dataEntry of tmpDataList)
          {
            let modelEntry = armorModelEntryTemplate;
            modelEntry = modelEntry.replaceAll('TRIMNAME', dataEntry.trimName);
            modelEntry = modelEntry.replaceAll('TRIMID', ((dataEntry.index || 0) + 1) / 1000);
            modelEntries = modelEntries + modelEntry;
          }
  
          armorModel = armorModel.replaceAll('ARMORMODELLINES', modelEntries);
          armorModel = armorModel.replaceAll('MATERIAL', material);
          armorModel = armorModel.replaceAll('ARMOR', armorType); 
  
          const armorModelName = `${material}_${armorType}.json`;
          item.file(armorModelName, armorModel);
  
  
          for (let dataEntry of tmpDataList)
          {
            let materialArmorModel = materialArmorModelTemplate;
            materialArmorModel = materialArmorModel.replaceAll('MATERIAL', material);
            materialArmorModel = materialArmorModel.replaceAll('ARMOR', armorType);
            materialArmorModel = materialArmorModel.replaceAll('TRIMNAME', dataEntry.trimName); 
            
            const materialArmorModelName = `${material}_${armorType}_${dataEntry.trimName}_trim.json`;
            item.file(materialArmorModelName, materialArmorModel);
          }
        }
      }
    }

    // Datapack
    // MCMETA file in root directory;
    let dataMcmeta = resourceMetadata;
    dataMcmeta = dataMcmeta.replaceAll('PACKNAME', packName);
    dataRoot.file("pack.mcmeta",dataMcmeta);

    let itemTagTrimMaterial = itemTagTrimMaterialTemplate; 
    let itemTagLines = '';

    for (let dataEntry of tmpDataList)
    {
      let thisLine = itemTagTrimMaterialLineTemplate;
      thisLine = thisLine.replaceAll('TRIMITEM',dataEntry.trimItem);
      itemTagLines = itemTagLines + thisLine;
    }

    itemTagTrimMaterial = itemTagTrimMaterial.replaceAll('ITEMTAGLINES',itemTagLines);
    items.file('trim_materials.json',itemTagTrimMaterial);

    for (let dataEntry of tmpDataList)
    {
      let trimDescription = trimDescriptionTemplate;
      trimDescription = trimDescription.replaceAll('TRIMNAME', dataEntry.trimName);
      trimDescription = trimDescription.replaceAll('CAPNAME', snakeToTitle(dataEntry.trimName));
      trimDescription = trimDescription.replaceAll('TRIMITEM', dataEntry.trimItem);
      trimDescription = trimDescription.replaceAll('TRIMCOLOR', dataEntry.gradientColors[7]);
      trimDescription = trimDescription.replaceAll('TRIMID', ((dataEntry.index || 0) + 1) / 1000);
      trim_material.file(`${dataEntry.trimName}.json`, trimDescription);
    }

    zip.generateAsync({type:"blob"}).then(function(content) {
      FileSaver.saveAs(content, `${packName}.zip`);
    });
  }

  const getDataFromChild = (index, data) => {
    let tmpList = dataList;
    tmpList[index] = {...data};
    setDataList([...tmpList]);
  }

  const addPicker = () => {
    setPickerCount(pickerCount + 1);
  }

  const removePicker = () => {
    setPickerCount(Math.max(pickerCount - 1, 1));
  }

  useEffect(function adjustPickers() {
    let tmpPickers = pickers;

    while (tmpPickers.length < pickerCount)
    {
      tmpPickers.push(<Picker key={`picker_${pickerCount - 1}`} sendDataToParent={getDataFromChild} childIndex={pickerCount - 1}/>);
    }
    while (tmpPickers.length > pickerCount)
    {
      tmpPickers.length = pickerCount;
    }
    setPickers([...tmpPickers]);
    
  }, [pickerCount])

  return (
    <div className="App">
      <div className="App-header">
        <p className="App-title-text">Minecraft Trim Material Generator</p>
      </div>
      <div className="App-body">
        <div className='App-row'>
          <div className='App-row-text'>Pack Name: </div>
          <input className='App-row-input' id="pack_name" value={packName} onChange={e => setPackName(e.target.value)}></input>
        </div>
        <PickerHolder pickers={pickers} />
        <div className="App-buttons">
          <button className='App-picker-button' onClick={removePicker}>-</button>
          <button className='App-download-button' onClick={saveResources}></button>
          <button className='App-picker-button' onClick={addPicker}>+</button>
        </div>
      </div>
    </div>
  )
}

const PickerHolder = props => {
  return <div>{props.pickers}</div>
}

function Picker({sendDataToParent, childIndex}){
  const [trimName, setTrimName] = useState("sea_foam");
  const [trimItem, setTrimItem] = useState("heart_of_the_sea");
  const [startColor, setStartColor] = useState('#aaFFbb');
  const [endColor, setEndColor] = useState('#0033FF');
  const [gradientColors, setGradientColors] = useState(generateGradient(startColor, endColor));

  const updateStartColor = (color) => {
    setStartColor(color);
    setGradientColors(generateGradient(startColor, endColor));
    updateData();
  }

  const updateEndColor = (color) => {
    setEndColor(color);
    setGradientColors(generateGradient(startColor, endColor));
    updateData();
  }

  const updateTrimName = (name) => {
    setTrimName(name);
    updateData();
  }

  const updateTrimItem = (item) => {
    setTrimItem(item);
    updateData();
  }

  const updateData = () => {
    let data = {};

    data.trimName = trimName;
    data.trimItem = trimItem;
    data.gradientColors = gradientColors;
    data.index = childIndex;

    sendDataToParent(childIndex, data);
  }

  useEffect(() => {
    updateData();
  }, []);
  
  return (
    <div className='App-picker'>
      <div className='App-picker-l'>
        <div className='App-picker-l-row'> 
          <div className='App-picker-l-row-l'>Trim Name:</div>
          <div className='App-picker-l-row-r'> 
          <input id="trim_name" value={trimName} onChange={e => updateTrimName(e.target.value)} />
          </div>
        </div>
        <div className='App-picker-l-row'> 
          <div className='App-picker-l-row-l'>Trim Material:</div>
          <div className='App-picker-l-row-r'> 
          <input id="trim_item" value={trimItem} onChange={e => updateTrimItem(e.target.value)}/>
          </div>
        </div>
        <div className='App-picker-l-row'>
          <div className='App-picker-l-row-l'>Trim Palette:</div>
          <div className='App-picker-l-row-r'> 
            <div id="palette_0" className='App-picker-l-row-b' style={{backgroundColor:gradientColors[0]}}/>
            <div id="palette_1" className='App-picker-l-row-b' style={{backgroundColor:gradientColors[1]}}/>
            <div id="palette_2" className='App-picker-l-row-b' style={{backgroundColor:gradientColors[2]}}/>
            <div id="palette_3" className='App-picker-l-row-b' style={{backgroundColor:gradientColors[3]}}/>
            <div id="palette_4" className='App-picker-l-row-b' style={{backgroundColor:gradientColors[4]}}/>
            <div id="palette_5" className='App-picker-l-row-b' style={{backgroundColor:gradientColors[5]}}/>
            <div id="palette_6" className='App-picker-l-row-b' style={{backgroundColor:gradientColors[6]}}/>
            <div id="palette_7" className='App-picker-l-row-b' style={{backgroundColor:gradientColors[7]}}/>
          </div>
        </div>
      </div>
      <div className='App-picker-r'>
        <div className='App-picker-r-u'>
          <HexColorPicker color={startColor} onChange={updateStartColor} />
        </div>
        <div className='App-picker-r-d'>
          <div className='App-picker-r-d-l'>Start Color:</div>
          <input className='App-picker-r-d-r' id="start_color" value={startColor} onChange={e => setStartColor(e.target.value)}/>
        </div>
      </div>
      <div className='App-picker-r'>
        <div className='App-picker-r-u'>
          <HexColorPicker color={endColor} onChange={updateEndColor} />
        </div>
        <div className='App-picker-r-d'>
          <div className='App-picker-r-d-l'>End Color:</div>
          <input className='App-picker-r-d-r' id="end_color" value={endColor} onChange={e => setEndColor(e.target.value)}/>
        </div>
      </div>
    </div>
  );
}

export default App;
